<style lang="less" scoped>
.room-management-vue {
  .weui_panel {
    margin-top: 0;
  }
  .room-item {
    padding: 0 10px;
    font-size: 14px;
    .item-agent, .item-customer {
      padding: 8px 0;
      line-height: 28px;
      a {
        padding: 0 30px;
      }
    }
  }
  .weui-form-preview {
    position: relative;
    background-color: #FFFFFF;
    &:before {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 1px;
      border-top: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
    }
    &:after {
      content: " ";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      height: 1px;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 0 100%;
      transform-origin: 0 100%;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
      left: 15px;
    }
    .weui-form-preview__hd {
      position: relative;
      padding: 10px 15px;
      text-align: right;
      line-height: 2.5em;
      &:after {
        content: " ";
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        height: 1px;
        border-bottom: 1px solid #D9D9D9;
        color: #D9D9D9;
        -webkit-transform-origin: 0 100%;
        transform-origin: 0 100%;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
        left: 15px;
      }
      .weui-form-preview__value {
        font-style: normal;
        font-size: 1.3em;
      }
    }
    .weui-form-preview__bd {
      padding: 10px 15px;
      font-size: .9em;
      text-align: right;
      color: #999999;
      line-height: 2;
    }
    .weui-form-preview__item {
      overflow: hidden;
      .weui-form-preview__label {
        float: left;
        margin-right: 1em;
        min-width: 4em;
        color: #999999;
        text-align: justify;
        text-align-last: justify;
      }
    }
    .weui-form-preview__value {
      display: block;
      overflow: hidden;
      word-break: normal;
      word-wrap: break-word;
    }
  }
}
</style>

<template>
  <div class="room-management-vue">
    <x-header :left-options="{showBack: true,backText: '返回'}">明细</x-header>
    <panel :list="roomDetail" :type="panelType"></panel>
    <group>
      <div class="room-item">
        <p class="item-agent vux-1px-b"><label>经纪人：</label><span>{{room.agent.name}}</span><a class="right weui_btn weui_btn_mini weui_btn_plain_primary" href="tel:{{room.agent.phoneNo}}">致电</a></p>
        <p class="item-customer"><label>租客：</label><span>{{room.customer}}</span></p>
      </div>
    </group>
    <group>
      <div v-if="orderType==1" class="weui-form-preview">
        <div class="weui-form-preview__hd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">看房时间</label>
            <em class="weui-form-preview__value">2016-11-10 10:10</em>
          </div>
        </div>
        <div class="weui-form-preview__bd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">创建时间</label>
            <span class="weui-form-preview__value">2016-11-10 10:10</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">看房人数</label>
            <span class="weui-form-preview__value">1人</span>
          </div>
        </div>
      </div>
      <div v-if="orderType==2" class="weui-form-preview">
        <div class="weui-form-preview__hd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">预定金</label>
            <em class="weui-form-preview__value">6900元</em>
          </div>
        </div>
        <div class="weui-form-preview__bd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">创建时间</label>
            <span class="weui-form-preview__value">2016-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">租客是否支付</label>
            <span class="weui-form-preview__value">已支付</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">支付方式</label>
            <span class="weui-form-preview__value">支付宝</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">月租金</label>
            <span class="weui-form-preview__value">3200元</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">押金</label>
            <span class="weui-form-preview__value">800元</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">付款方式</label>
            <span class="weui-form-preview__value">付三押一</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">合同起租日</label>
            <span class="weui-form-preview__value">2016-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">合同结束日</label>
            <span class="weui-form-preview__value">2017-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">最晚签约日</label>
            <span class="weui-form-preview__value">2016-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">备注</label>
            <span class="weui-form-preview__value">预定备注</span>
          </div>
        </div>
      </div>
      <div v-if="orderType==3" class="weui-form-preview">
        <div class="weui-form-preview__hd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">租客首付款</label>
            <em class="weui-form-preview__value">6900元</em>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">佣金支出</label>
            <em class="weui-form-preview__value">2000元</em>
          </div>
        </div>
        <div class="weui-form-preview__bd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">创建时间</label>
            <span class="weui-form-preview__value">2016-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">租客电话</label>
            <span class="weui-form-preview__value">021-89898953</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">实际租金</label>
            <span class="weui-form-preview__value">3200/月</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">付款方式</label>
            <span class="weui-form-preview__value">付三押一</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">起租日</label>
            <span class="weui-form-preview__value">2016-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">到期日</label>
            <span class="weui-form-preview__value">2017-11-10 10:00</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">租约期限</label>
            <span class="weui-form-preview__value">12个月</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label">备注</label>
            <span class="weui-form-preview__value">签约备注</span>
          </div>
        </div>
      </div>
    </group>
    <loading :show="loading" :text="loadingText"></loading>
  </div>
</template>
<script>
import XHeader from 'vux-components/x-header'
import Group from 'vux-components/group'
import Box from 'vux-components/box'
import Panel from 'vux-components/panel'
import Card from 'vux-components/card'
import Loading from 'vux-components/loading'
import XButton from 'vux-components/x-button'
import Request from '../../js/request'
import Manba from 'manba'

export default {
  data() {
    return {
      panelType: '1',
      orderType: 2,
      orderMap: {
        1: '预约',
        2: '预定',
        3: '签约'
      },
      keyword: '',
      autoFixed: false,
      tabs: [],
      rooms: [],
      loading: false,
      loadingText: '加载中...',
      roomDetail: [{
        src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
        title: '标题一',
        desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
        url: '/'
      }],
    }
  },
  components: {
    XHeader,
    Group,
    Box,
    Panel,
    Card,
    Loading,
    XButton
  },
  methods: {
  },
  ready() {
    const that = this;
  }
}
</script>



